<h1 id="linux上安装openjdk" tabindex="-1"><a class="header-anchor" href="#linux上安装openjdk" aria-hidden="true">#</a> Linux上安装openjdk</h1>
<h2 id="centos安装openjdk" tabindex="-1"><a class="header-anchor" href="#centos安装openjdk" aria-hidden="true">#</a> Centos安装openjdk</h2>
<p><a href="https://openjdk.java.net/install/" target="_blank" rel="noopener noreferrer">openjdk官网地址<OutboundLink/></a></p>
<p>安装官网的命令会缺少javac命令</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">su</span> -c <span class="token string">"yum install java-1.8.0-openjdk"</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>必须要安装全部相关依赖</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>yum -y <span class="token function">install</span> java-1.8.0-openjdk*
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>安装完成后查找安装到了哪里，查看软连接的实际地址</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">which</span> java
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>https://www.jianshu.com/p/aa74930b5beb</p>
